.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/*Barra de navegacion*/
/*//////////////////////////////////////////*/

.barraNav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  list-style: none;
  border-bottom: 3px solid black;
  margin: 0;
}

.barraNav li {
  display: block;
  text-transform: uppercase;
  font-weight: 500;
}

.barraNav a:hover {
  background: black;
  color: white;
}

.barraNav a {
  display: inline-block;
  text-decoration: none;
  padding: 20px 30px;
  -webkit-transition: color 200ms, background 200ms;
  -o-transition: color 200ms, background 200ms;
  transition: color 200ms, background 200ms;
}

#page-body {
  width: 70%;
  margin: auto;
}

.list-item {
  text-decoration: none;
  color: rgb(160, 114, 114);
}

.list-container {
  text-align: left;
  border-bottom: 2px solid #333;
}

/*COMENTARIOS*/

.commentSection {
  background: #f1faf5;
  margin: 20px;
  text-align: left;
  border-radius: 10px;
  border: 3px solid rgb(172, 168, 168);
}

.commentSection h3 {
  padding: 20px 0px;
  text-transform: uppercase;
  font-size: 1em;
  text-align: center;
  border-bottom: 2px solid black;
}

.comment {
  padding: 10px;
  margin: 10px;
  border: 3px solid red;
  border-radius: 10px;
}

/*SECCION DE VOTOS*/

.upvoteSection {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

/*SECCION DE COMENTARIOS*/

#add-comment-form {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 50vw;
  margin: auto;
  padding: 20px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  min-height: 40vh;
  color: #fff;
  background: #000;
}

#add-comment-form h3 {
  text-transform: uppercase;
  font-size: 1.5em;
}

#add-comment-form input {
  border: none;
  height: 30px;
}

#add-comment-form input,
#add-comment-form textarea {
  display: block;
  min-width: 300px;
  max-width: 50vw;
  width: 400px;
  margin: 20px;
  border-bottom: 3px solid orange;
}

#add-comment-form input:placeholder,
#add-comment-form textarea:placeholder {
  color: #999;
}

#add-comment-form input:focus,
#add-comment-form textarea:focus {
  outline: none;
}

#add-comment-form button {
  justify-self: flex-end;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



